.o-header{
    /* display: flex; */
    background-color: #f01c24;
    padding: 50px 80px;
    justify-content: space-between;
    align-items: flex-end;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.o-logo img{
    width: 100%;

}

.o-search{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding-bottom: 10px; 
} 


.o-search input[type=text] {
    width: 75%;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px 0px 0px 4px;
    font-size: 16px;
    background-color: white;
    background-image: url('../Resources/superheroes.svg'); 
    background-position: 5px center;
    background-size: 30px; 
    background-repeat: no-repeat;
    padding: 12px 20px 12px 50px;
    -webkit-transition: width 0.5s ease-in-out;
  }
  
  .o-search input[type=text]:focus {
    width: 100%;
    background-color: rgb(255, 255, 215);
    background-size: 30px;
  }

  .o-btnSearch{
    height: 52px;
    border: 0px;
    background-position: center;
    background-size: 30px;
    background-repeat: no-repeat;
    background-image: url('../Resources/buscar-negro.svg');
    
    padding: 0 25px;
    background-color: rgb(218, 216, 216);
  }

  .o-btnSearch:hover{
      background-image: url('../Resources/buscar-rojo.svg');
      background-color: #fff;
      background-size: 28px;
      
  }

  .o-btnClear{
    height: 52px;
    border: 0px;
    background-position: center;
    background-size: 30px;
    background-repeat: no-repeat;
    background-image: url('../Resources/borrar-negro.svg');
    border-left: 2px solid #ccc;
    border-radius: 0px 4px 4px 0;
    padding: 0 25px;
    background-color: rgb(218, 216, 216);
  }

  .o-btnClear:hover{
      background-image: url('../Resources/borrar-rojo.svg');
      background-color: #fff
  }



@media (max-width: 600px){
  .o-header{
    padding: 30px 30px;

  }

  .o-logo{
    margin: auto;
    padding-bottom: 20px;
    text-align: center;
}

.o-search input[type=text] {
  width: 65%;
}

.o-search input[type=text]:focus {
  width: 100%;}
}